	module.exports = {"data":{"markdownRemark":{"html":"<p>えー、はじめまして。MAKINAと申すモノです。 とりあえずリニューアル記念、と言うことで日記代わりにコラムまがいのこんなコーナーを始めてみました。 名付けて『<strong>当サイトにまつわるどうでもいい話</strong>』。 その名の通り<em>本当にどうでもいい話しか扱いませんし、これからどういう話に逸れていくか知れません</em>。  思えばふと思った雑学とか、このサイトの裏話とかどうでもいいことだけど一応説明しておくかみたいな話とか、先週プレーリードッグの話をしたかと思うと今週は突然Direct3Dを用いたプログラミングの話になったりなど。(参考までに、筆者はプレーリードッグは生では見たことありませんが) このコラムは週に1度を目安に週末辺りに更新しようかと思ってますが、何か書きたいことがあったらその都度リアルタイムで書くつもりであります。 さて、イントロはこの辺にして、そろそろどうでもいい話に入りましょうか。過去に3回程名前を変えましたが、今回でサイトの名前まで変えるほどの大リニューアルは4回目。 つーわけで「Force」と「4th」を引っ掛けてみました。もうお分かりかもしれませんがこのロゴ、「<em>F</em>orce.<em>Y</em>」の<em>F</em>と<em>Y</em>を無理矢理くっつけたのをデザインしただけなんですね。あははのは。</p>\n<h2>更新情報</h2>\n<ul>\n<li>独自ドメイン取得＆リニューアル。</li>\n<li>Software Forceにソフトウェアを1つ追加。</li>\n</ul>","frontmatter":{"cover":null,"date":"2002-08-08T00:00:00+09:00","strDate":"2002/8/7","title":"はじめに"}}},"pathContext":{"path":"/ja/blog/2002/08/08-はじめに/","langKey":"ja"}}

